<script setup>
import gql from "graphql-tag";
import {useQuery} from "@vue/apollo-composable";
import {ref, watch} from "vue";

const selectOptions = ref([])
const select = ref('projects')

const ALL_PROJECT_CATEGORIES_QUERY = gql`
			query {
				projectCollection {
					items {
						category,
					}
				}
			}
		`

const { result } = useQuery(ALL_PROJECT_CATEGORIES_QUERY)

watch(result, value => {
	const categories = value.projectCollection.items.map(item => item.category)
	selectOptions.value = categories;
})

</script>

<template>
  <select
    :id="select"
    :name="select"
    class="font-general-medium
				px-4
				py-2
				border-1 border-gray-200
				dark:border-secondary-dark
				rounded-lg
				text-sm
				sm:text-md
				bg-secondary-light
				dark:bg-ternary-dark
				text-primary-dark
				dark:text-ternary-light
			"
    @change="$emit('filter', $event.target.value)"
  >
    <option
      value
      class="text-sm sm:text-md"
    >
      All Projects
    </option>
    <option
      v-for="option in selectOptions"
      :key="option"
      :value="option"
      class="sm:text-md"
    >
      {{ option }}
    </option>
  </select>
</template>

<style lang="scss" scoped></style>
