<script>
import feather from "feather-icons"
import ContentfulRichTextRenderer from "@/components/reusable/ContentfulRichTextRenderer.vue";
import {BLOCKS, INLINES} from "@contentful/rich-text-types";

const richTextAboutClientOptions = {
  renderNode: {
    [BLOCKS.PARAGRAPH]: (node, next) =>
      `<p class="block font-general-regular text-ternary-dark dark:text-ternary-light">${next(node.content)}</p>`,
    [BLOCKS.HEADING_1]: (node, next) =>
      `<h1 class="font-general-medium text-2xl text-ternary-dark dark:text-ternary-light mb-2">${next(node.content)}</h1>`,
    [INLINES.HYPERLINK]: (node, next) => {
      return `<a href="${node.data.uri}" target="_blank">${next(node.content)}</a>`;
    },
    [BLOCKS.UL_LIST]: (node, next) => {
      return `<ul class="list-none">${next(node.content)}</ul>`;
    }
  },
};

const richTextContentOptions = (links) => {
  // create an asset map
  const assetMap = new Map();

  // create an entry map
  const entryBlockMap = new Map();

  if (links) {
    // loop through the assets and add them to the map
    for (const asset of links.assets.block) {
      assetMap.set(asset.sys.id, asset);
    }


    // loop through the block linked entries and add them to the map
    for (const entry of links.entries.block) {
      entryBlockMap.set(entry.sys.id, entry);
    }
  }

  return {
    renderNode: {
      [BLOCKS.UL_LIST]: (node, next) => {
        return `<ul class="list-disc ml-5 mb-5">${next(node.content)}</ul>`;
      },
      [BLOCKS.EMBEDDED_ENTRY]: (node, children) => {
        // find the entry in the entryMap by ID
        const entry = entryBlockMap.get(node.data.target.sys.id);
        console.log(node, entry)
        if (entry.__typename === "VideoEmbed") {
          const url = entry.embedUrl;
          // Only process youtube links
          if (url.includes("youtube.com")) {
            // Extract videoId from the URL
            const match = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#&?]*).*/.exec(
              url
            )
            const videoId =
              match && match[7].length === 11 ? match[7] : null
            return `
            <section class="video-container">
              <iframe
                class="video"
                title='https://youtube.com/embed/${videoId}'
                src='https://youtube.com/embed/${videoId}'
                allow="accelerometer; encrypted-media; gyroscope; picture-in-picture"
                frameBorder="0"
                allowFullScreen
              ></iframe>
            </section>
          `;
          } else {
            return `<a href="${node.data.uri}" target="_blank">${next(node.content)}</a>`;
          }
        }
      },
      [INLINES.HYPERLINK]: (node, next) => {
        return `<a href="${node.data.uri}" target="_blank">${next(node.content)}</a>`;
      },
      [BLOCKS.EMBEDDED_ASSET]: (node, next) => {
        // find the asset in the assetMap by ID
        const asset = assetMap.get(node.data.target.sys.id);

        // render the asset accordingly
        return `
          <img src="${asset.url}" alt="My image alt text" />
        `;
      },
    }
  }
};

export default {
  components: {
    ContentfulRichTextRenderer,
  },
  props: ["project"],
  data() {
    return {
      richTextAboutClientOptions,
      richTextContentOptions
    }
  },
  mounted() {
    feather.replace()
  },
  updated() {
    feather.replace()
  },
}
</script>

<template>
  <div class="block sm:flex gap-0 sm:gap-10 mt-14">
    <!-- Single project left section details -->
    <div class="w-full sm:w-1/3 text-left">
      <!-- Single project client details -->
      <div
        v-if="project?.aboutClient"
        class="mb-7"
      >
        <p class="font-general-medium text-2xl text-secondary-dark dark:text-secondary-light mb-2">
          About Client
        </p>
        <ContentfulRichTextRenderer
          :document="project?.aboutClient?.json"
          :options="richTextAboutClientOptions"
        />
      </div>

      <!-- Single project technologies -->
      <div
        v-if="project?.toolsTechnologies"
        class="mb-7"
      >
        <p class="font-general-medium text-2xl text-ternary-dark dark:text-ternary-light mb-2">
          Tools & Technologies
        </p>
        <p class="font-general-regular text-primary-dark dark:text-ternary-light">
          {{ project?.toolsTechnologies.join(", ") }}
        </p>
      </div>

      <!-- Live Demo -->
      <div
        v-if="project?.liveDemo"
        class="mb-7"
      >
        <p class="font-general-medium text-2xl text-ternary-dark dark:text-ternary-light mb-2">
          Live Demo
        </p>
        <a
          :href="project?.liveDemo"
          target="_blank"
          class="font-general-regular text-primary-dark dark:text-ternary-light hover:underline cursor-pointer truncate"
        >
          {{ project?.liveDemo }}
        </a>
      </div>

      <!-- Single project code repositories -->
      <div
        v-if="project?.codeRepositories"
        class="mb-7"
      >
        <p class="font-general-medium text-2xl text-secondary-dark dark:text-secondary-light mb-2">
          Code Repositories
        </p>
        <ContentfulRichTextRenderer
          :document="project?.codeRepositories?.json"
          :options="richTextAboutClientOptions"
        />
      </div>

      <!-- Additional reading -->
      <div
        v-if="project?.additionalReading"
        class="mb-7"
      >
        <p class="font-general-medium text-2xl text-ternary-dark dark:text-ternary-light mb-2">
          More About {{ project?.title }}
        </p>
        <ul class="list-disc ml-5 mb-5">
          <li
            v-for="(link, index) in project?.additionalReading"
            :key="index"
          >
            <a
              :href="link"
              target="_blank"
              class="font-general-regular text-primary-dark dark:text-ternary-light hover:underline cursor-pointer"
            >
              {{ link }}
            </a>
          </li>
        </ul>
      </div>
    </div>

    <!-- Single project right section details -->
    <div class="w-full sm:w-2/3 text-left mt-10 sm:mt-0">
      <ContentfulRichTextRenderer
        :document="project?.body?.json"
        :options="richTextContentOptions(project?.body?.links)"
      />
    </div>
  </div>
</template>
