<style scoped>
>>> h1 {
    @apply text-2xl;
}

>>> h2 {
    @apply text-xl;
}

>>> p {
    @apply mb-2 inline-block;
}

>>> li > p {
    @apply mb-0;
}

>>> a {
    @apply hover:underline cursor-pointer;
}
</style>

<script>
import { documentToHtmlString } from '@contentful/rich-text-html-renderer';

// TODO render youtube video: https://miguelcrespo.co/posts/rendering-youtube-videos-using-rich-text-contentful

export default {
  props: ['document', 'options'],
  methods: {
    documentToHtmlString,
  }
}
</script>

<template>
  <div v-html="documentToHtmlString(document, options)"></div>
</template>