<script>
export default {
	props: ['updateDate', 'category', 'title'],
  methods: {
    formattedDate() {
      // Create a new Date object from the input date string
      const dateObj = new Date(this.updateDate);

      // Define an array with month names
      const monthNames = [
        "January", "February", "March", "April", "May", "June",
        "July", "August", "September", "October", "November", "December"
      ];

      // Extract the day, month, and year from the date object
      const day = dateObj.getUTCDate();
      const month = monthNames[dateObj.getUTCMonth()];
      const year = dateObj.getUTCFullYear();

      // Create the formatted date string
      return `${month} ${day}, ${year}`
    }
  }
};
</script>

<template>
  <div>
    <!-- Project heading and meta info -->
    <p
      class="font-general-medium text-left text-3xl sm:text-4xl font-bold text-primary-dark dark:text-primary-light mt-14 sm:mt-20 mb-7"
    >
      {{ title }}
    </p>
    <div class="flex">
      <div class="flex items-center mr-10">
        <i
          data-feather="clock"
          class="w-4 h-4 text-ternary-dark dark:text-ternary-light"
        />
        <span
          class="font-general-medium ml-2 leading-none text-primary-dark dark:text-primary-light"
        >{{ formattedDate() }}</span>
      </div>
      <div class="flex items-center">
        <i
          data-feather="tag"
          class="w-4 h-4 text-ternary-dark dark:text-ternary-light"
        />
        <span
          class="font-general-medium ml-2 leading-none text-primary-dark dark:text-primary-light"
        >{{ category }}</span>
      </div>
    </div>
  </div>
</template>
