<script setup>
import feather from "feather-icons"
import ProjectHeader from "../components/projects/ProjectHeader.vue"
import ProjectInfo from "../components/projects/ProjectInfo.vue"
import gql from "graphql-tag";
import {onBeforeUnmount, onUpdated, watch} from "vue";
import {useQuery} from "@vue/apollo-composable";
import {useRoute} from "vue-router";

console.log("Load SingleProject.vue")

const PROJECT_BY_SLUG_QUERY = gql`
  query ($slug: String!) {
    projectCollection(limit: 1, where: { slug: $slug }) {
      items {
        slug
        title
        updateDate
        body {
          json
          links {
            entries {
              block {
                sys {
                  id
                }
                __typename
                ... on VideoEmbed {
                  embedUrl
                  title
                }
              }
            }
            assets {
              block {
                sys {
                  id
                }
                url
                title
                width
                height
                description
              }
            }
          }
        }
        aboutClient {
            json
        }
        toolsTechnologies
        liveDemo
        codeRepositories {
            json
        }
        additionalReading
        category
        icon {
          url
          title
          description
          contentType
          width
          height
        }
      }
    }
  }
`;

const route = useRoute();

// TODO Check if can be moved to setup or created lifecycle hook
// Assuming you have the 'slug' variable defined with the desired slug value
const { loading, error, result } = useQuery(PROJECT_BY_SLUG_QUERY, {
  slug: route.params.slug,
});

onBeforeUnmount(() => {
  feather.replace()
});

onUpdated(() => {
  feather.replace()
});
</script>

<template>
  <div
    v-if="result?.projectCollection?.items[0]"
    class="container mx-auto mt-10 sm:mt-20"
  >
    <!-- Project header -->
    <ProjectHeader
      :update-date="result.projectCollection.items[0].updateDate"
      :category="result.projectCollection.items[0].category"
      :title="result.projectCollection.items[0].title"
    />

    <!-- Project gallery -->
    <!--    <ProjectGallery :project-images="projectImages" />-->

    <!-- Project information -->
    <ProjectInfo
      :project="result.projectCollection.items[0]"
    />

    <!-- Project related projects -->
    <!--    <ProjectRelatedProjects :related-project="relatedProject" />-->
  </div>
  <img
    v-else-if="loading"
    src="../assets/gifs/loading.gif"
  >
  <h1 v-else-if="error">Error</h1>
</template>

<style scoped></style>
